@font-face {
  font-family: 'Poppins-Medium';
  src: url("fonts/Poppins-Medium.eot");
  src: local('☺'), url("fonts/Poppins-Medium.woff") format('woff'), url("fonts/Poppins-Medium.ttf") format('truetype'), url("fonts/Poppins-Medium.svg") format('svg');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'JosefinSans-Regular';
  src: url("fonts/regular/JosefinSans-Regular.eot");
  src: local('☺'), url("fonts/regular/JosefinSans-Regular.woff") format('woff'), url("fonts/regular/JosefinSans-Regular.ttf") format('truetype'), url("fonts/regular/JosefinSans-Regular.svg") format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'JosefinSans-Light';
  src: url("fonts/light/JosefinSans-Light.eot");
  src: local('☺'), url("fonts/light/JosefinSans-Light.woff") format('woff'), url("fonts/light/JosefinSans-Light.ttf") format('truetype'), url("fonts/light/JosefinSans-Light.svg") format('svg');
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: 'JosefinSans-SemiBold';
  src: url("fonts/semibold/JosefinSans-SemiBold.eot");
  src: local('☺'), url("fonts/semibold/JosefinSans-SemiBold.woff") format('woff'), url("fonts/semibold/JosefinSans-SemiBold.ttf") format('truetype'), url("fonts/semibold/JosefinSans-SemiBold.svg") format('svg');
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: 'JosefinSans-Bold';
  src: url("fonts/bold/JosefinSans-Bold.eot");
  src: local('☺'), url("fonts/bold/JosefinSans-Bold.woff") format('woff'), url("fonts/bold/JosefinSans-Bold.ttf") format('truetype'), url("fonts/bold/JosefinSans-Bold.svg") format('svg');
  font-weight: 700;
  font-style: normal;
}
body {
  background-color: #fff;
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}
ul[class],
ol[class],
li {
  list-style: none;
}
ul[class],
ol[class] {
  padding: 0;
}
a:not([class]) {
  text-decoration-skip-ink: auto;
}
img {
  max-width: 100%;
  display: block;
}
input,
button,
textarea,
select {
  font: inherit;
}
h1 {
  margin-top: 1em;
  font-size: 5em;
}
h2 {
  margin: 0;
}
p {
  margin: 0;
}
a {
  color: #000;
  text-decoration: none;
}
.section {
  margin: 10em 0;
}
.container {
  width: 100%;
  max-width: 1170px;
  padding: 0 15px;
  margin: 0 auto;
  overflow: hidden;
}
.header .header-inner {
  text-align: center;
}
